import * as React from 'react';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import { MenuItem } from '@material-ui/core';
import { Button } from '@mui/material';
import { useContext } from 'react';
import DeliveryContext from '../context/DeliveryProvider';


export default function Form({ handleNext } = this) {

    const { setSenderInfo } = useContext(DeliveryContext);

    const [modality, setModality] = React.useState('Same Day');
    const [name, setName] = React.useState('Same Day');
    const [email, setEmail] = React.useState('Same Day');
    const [whatsapp, setWhatsapp] = React.useState('Same Day');
    const [cep, setCep] = React.useState('Same Day');
    const [adress, setAdress] = React.useState('Same Day');
    const [number, setNumber] = React.useState('Same Day');
    const [complement, setComplement] = React.useState('Same Day');
    const [district, setDistrict] = React.useState('Same Day');
    const [city, setCity] = React.useState('Same Day');
    const [uf, setUf] = React.useState('Same day');
    const [product, setProduct] = React.useState('Envelopes');
    const [saveAdress, setSaveAdress] = React.useState(false);

    const data = {
        modality,
        name,
        email,
        whatsapp,
        cep,
        adress,
        number,
        complement,
        district,
        city,
        uf,
        product,
        saveAdress,
    }

    function handleSubmit() {
        if (modality != '' && name != '' && email != '' && whatsapp != '' && cep != '' && adress != '' && number != '' && complement != '' && district != '' && city != '' && uf != '' && product != '' && saveAdress != '') {
            setTimeout(handleNext(), 2000);
            setSenderInfo(data)
        } else {
            alert('Por favor, preencha todos os campos corretamente!')
        }
    }

    return (
        <React.Fragment>
            <Typography variant="h6" gutterBottom>
                Informações - Remetente
            </Typography>
            <Typography variant="p" color="#ff8a00" gutterBottom>
                Insira o endereço de ORIGEM da encomenda
            </Typography>
            <Grid container spacing={3} paddingTop={2}>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        label="Seu nome completo"
                        fullWidth
                        autoComplete="given-name"
                        variant="outlined"
                        value={name}
                        onChange={(e) => { setName(e.target.value) }}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        label="Seu email"
                        fullWidth
                        autoComplete="family-name"
                        variant="outlined"
                        value={email}
                        onChange={e => setEmail(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        label="Seu Whatsapp (DDD)"
                        fullWidth
                        autoComplete="shipping address-line1"
                        variant="outlined"
                        value={whatsapp}
                        onChange={e => setWhatsapp(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        label="CEP"
                        fullWidth
                        autoComplete="shipping postal-code"
                        variant="outlined"
                        value={cep}
                        onChange={e => setCep(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        label="Endereço"
                        fullWidth
                        autoComplete="shipping address-line2"
                        variant="outlined"
                        value={adress}
                        onChange={e => setAdress(e.target.value)}
                    />
                </Grid>
                <Grid item xs={6} sm={3}>
                    <TextField
                        label="Número"
                        fullWidth
                        autoComplete="shipping address-line2"
                        variant="outlined"
                        value={number}
                        onChange={e => setNumber(e.target.value)}
                    />
                </Grid>
                <Grid item xs={6} sm={3}>
                    <TextField
                        label="Complemento"
                        fullWidth
                        autoComplete="shipping address-line2"
                        variant="outlined"
                        value={complement}
                        onChange={e => setComplement(e.target.value)}
                    />
                </Grid>
                <Grid item xs={6} sm={6}>
                    <TextField
                        label="Bairro"
                        fullWidth
                        autoComplete="shipping address-line2"
                        variant="outlined"
                        value={district}
                        onChange={e => setDistrict(e.target.value)}
                    />
                </Grid>
                <Grid item xs={4}>
                    <TextField
                        required
                        label="Cidade"
                        fullWidth
                        autoComplete="shipping address-level2"
                        variant="outlined"
                        value={city}
                        onChange={e => setCity(e.target.value)}
                    />
                </Grid>
                <Grid item xs={6} sm={2}>
                    <TextField
                        label="UF"
                        fullWidth
                        variant="outlined"
                        value={uf}
                        onChange={e => setUf(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        label="O que deseja enviar?"
                        fullWidth
                        select
                        variant="outlined"
                        value={product}
                        onChange={e => setProduct(e.target.value)}
                    >
                        <MenuItem key={1} value={'Caixa'}>Caixa</MenuItem>
                        <MenuItem key={2} value={'Envelopes'}>Envelopes</MenuItem>
                        <MenuItem key={3} value={'Documentos'}>Documentos</MenuItem>
                        <MenuItem key={4} value={'Outros'}>Outros</MenuItem>
                    </TextField>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        label="Modalidade"
                        fullWidth
                        select
                        variant="outlined"
                        value={modality}
                        onChange={e => setModality(e.target.value)}
                    >
                        <MenuItem key={1} value={'Same Day'}>Same Day (Entregue no mesmo dia)</MenuItem>
                        <MenuItem key={2} value={'Next Day'}>Next Day (13h do dia seguinte)</MenuItem>
                    </TextField>
                </Grid>
                <Grid item xs={12}>
                    <FormControlLabel
                        control={<Checkbox color="secondary" name="saveAddress" value="yes" />}
                        label="Salvar este endereço como favorito"
                        value={saveAdress}
                        onChange={e => setSaveAdress(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12}>
                    <Button variant='contained' onClick={() => { handleSubmit() }}>
                        Próximo
                    </Button>
                </Grid>
            </Grid>
        </React.Fragment>
    );
}