import * as React from 'react';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import { Button } from '@material-ui/core';
import { useContext } from 'react';
import DeliveryContext from '../context/DeliveryProvider';

export default function RecipientForm({ handleNext, handleBack } = this) {

    const { setRecipientInfo, calculateValue } = useContext(DeliveryContext);

    const [name, setName] = React.useState('Same Day');
    const [email, setEmail] = React.useState('Same Day');
    const [whatsapp, setWhatsapp] = React.useState('Same Day');
    const [cep, setCep] = React.useState('Same Day');
    const [adress, setAdress] = React.useState('Same Day');
    const [number, setNumber] = React.useState('Same Day');
    const [complement, setComplement] = React.useState('Same Day');
    const [district, setDistrict] = React.useState('Same Day');
    const [city, setCity] = React.useState('Same Day');
    const [uf, setUf] = React.useState('Same Day');
    const [saveAdress, setSaveAdress] = React.useState(false);

    const data = {
        name,
        email,
        whatsapp,
        cep,
        adress,
        number,
        complement,
        district,
        city,
        uf,
        saveAdress,
    }

    function handleSubmit() {
        if (name != '' && email != '' && whatsapp != '' && cep != '' && adress != '' && number != '' && complement != '' && district != '' && city != '' && uf != '' && saveAdress != '') {
            setTimeout(handleNext(), 2000);
            setRecipientInfo(data);
            calculateValue();
        } else {
            alert('Por favor, preencha todos os campos corretamente!')
        }
    }

    return (
        <React.Fragment>
            <Typography variant="h6" gutterBottom>
                Informações - Destinatário
            </Typography>
            <Typography variant="p" color="#ff8a00" gutterBottom>
                Insira o endereço de DESTINO da encomenda
            </Typography>
            <Grid container spacing={3} paddingTop={2}>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        label="Nome completo"
                        fullWidth
                        autoComplete="given-name"
                        variant="outlined"
                        value={name}
                        onChange={e => setName(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        label="Email"
                        fullWidth
                        autoComplete="family-name"
                        variant="outlined"
                        value={email}
                        onChange={e => setEmail(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        label="Whatsapp (DDD)"
                        fullWidth
                        autoComplete="shipping address-line1"
                        variant="outlined"
                        value={whatsapp}
                        onChange={e => setWhatsapp(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        label="CEP"
                        fullWidth
                        autoComplete="shipping postal-code"
                        variant="outlined"
                        value={cep}
                        onChange={e => setCep(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        label="Endereço"
                        fullWidth
                        autoComplete="shipping address-line2"
                        variant="outlined"
                        value={adress}
                        onChange={e => setAdress(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12} sm={3}>
                    <TextField
                        label="Número"
                        fullWidth
                        autoComplete="shipping address-line2"
                        variant="outlined"
                        value={number}
                        onChange={e => setNumber(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12} sm={3}>
                    <TextField
                        label="Complemento"
                        fullWidth
                        autoComplete="shipping address-line2"
                        variant="outlined"
                        value={complement}
                        onChange={e => setComplement(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        label="Bairro"
                        fullWidth
                        autoComplete="shipping address-line2"
                        variant="outlined"
                        value={district}
                        onChange={e => setDistrict(e.target.value)}
                    />
                </Grid>

                <Grid item xs={12} sm={3}>
                    <TextField
                        required
                        label="Cidade"
                        fullWidth
                        autoComplete="shipping address-level2"
                        variant="outlined"
                        value={city}
                        onChange={e => setCity(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12} sm={3}>
                    <TextField
                        label="UF"
                        fullWidth
                        variant="outlined"
                        value={uf}
                        onChange={e => setUf(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12}>
                    <FormControlLabel
                        control={<Checkbox color="secondary" name="saveAddress" value={saveAdress} onChange={e => setSaveAdress(e.target.value)} />}
                        label="Salvar este endereço como favorito"
                    />
                </Grid>
                <Grid item xs={12}>
                    <Button variant='contained' onClick={() => { handleBack() }}>
                        Voltar
                    </Button>
                    <Button variant='contained' onClick={() => { handleSubmit() }}>
                        Calcular entrega
                    </Button>
                </Grid>
            </Grid>
        </React.Fragment>
    );
}