import axios from 'axios';
import React, { useEffect } from 'react'
import { useState } from 'react';
import Services, { api } from '../Services';

const DeliveryContext = React.createContext({})

function DeliveryProvider({ children }) {

    //Sender informations:
    const [senderInfo, setSenderInfo] = React.useState({});
    //Recipient information:
    const [recipientInfo, setRecipientInfo] = React.useState({});
    const [deliveryInfos, setDeliveryInfos] = useState({});
    const [price, setPrice] = useState();

    const data = {
        "reference_id": "ex-00001",
        "description": "Motivo do pagamento",
        "amount": {
            "value": 1000,
            "currency": "BRL"
        },
        "payment_method": {
            "type": "BOLETO",
            "boleto": {
                "due_date": "2024-12-31",
                "instruction_lines": {
                    "line_1": "Pagamento processado para DESC Fatura",
                    "line_2": "Via PagSeguro"
                },
                "holder": {
                    "name": "Jose da Silva",
                    "tax_id": "22222222222",
                    "email": "jose@email.com",
                    "address": {
                        "street": "Avenida Brigadeiro Faria Lima",
                        "number": "1384",
                        "locality": "Pinheiros",
                        "city": "Sao Paulo",
                        "region": "Sao Paulo",
                        "region_code": "SP",
                        "country": "Brasil",
                        "postal_code": "01452002"
                    }
                }
            }
        },
        "notification_urls": [
            "https://yourserver.com/nas_ecommerce/277be731-3b7c-4dac-8c4e-4c3f4a1fdc46/"
        ]
    }

    function calculateValue() {

        const senderData = {
            cep: senderInfo.cep,
            adress: senderInfo.adress,
            number: senderInfo.number,
            complement: senderInfo.complement,
            district: senderInfo.district,
            city: senderInfo.city,
            uf: senderInfo.uf
        }

        const recipientData = {
            cep: recipientInfo.cep,
            adress: recipientInfo.adress,
            number: recipientInfo.number,
            complement: recipientInfo.complement,
            district: recipientInfo.district,
            city: recipientInfo.city,
            uf: recipientInfo.uf
        }

        const deliveryInfo = {
            product: senderInfo.product,
            modality: senderInfo.modality
        }

        api.get('calculaEnvio.php', { senderData, recipientData, deliveryInfo })
            .then(res => {
                if (res.data.success) {
                    setPrice(res.data.value);
                    setDeliveryInfos(deliveryInfo);
                } else {
                    setPrice(9999999999);
                }
            })
            .catch(err => {
                console.log(err);
            });
    }

    function executeDelivery() {
        axios.post('', {
            headers: {
                'Authorization': '{{token}}',
                'Content-Type': 'application/json'
            },

        })
            .then((response) => {
                /* dispatch({ type: FOUND_USER, data: response.data[0] }) */
            })
            .catch((error) => {
                /* dispatch({ type: ERROR_FINDING_USER }) */
            })
    }

    return (
        <DeliveryContext.Provider
            value={{
                senderInfo,
                setSenderInfo,
                recipientInfo,
                setRecipientInfo,
                calculateValue,
                deliveryInfos,
                price
            }}
        >
            {children}
        </DeliveryContext.Provider>
    )
}

export default DeliveryContext;

export { DeliveryProvider }