import * as React from 'react';
import Typography from '@mui/material/Typography';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Grid from '@mui/material/Grid';
import { Button, Divider } from '@material-ui/core';
import { useContext } from 'react';
import DeliveryContext from '../context/DeliveryProvider';


export default function Review({ handleNext, handleBack } = this) {

  const { senderInfo, recipientInfo, deliveryInfos, price } = useContext(DeliveryContext);

  const senderAdress = `${senderInfo.adress}, ${senderInfo.number}, ${senderInfo.district}, ${senderInfo.city}, ${senderInfo.cep}, ${senderInfo.uf}`;

  const recipientAdress = `${recipientInfo.adress}, ${recipientInfo.number}, ${recipientInfo.district}, ${recipientInfo.city}, ${recipientInfo.cep}, ${recipientInfo.uf}`;

  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
        Revise seu envio
      </Typography>
      <Typography variant="p" color="#ff8a00" gutterBottom>
        Revise a descrição de seu envio
      </Typography>
      <Grid container spacing={3} paddingTop={2}>
        <Grid item xs={12}>
          <Divider />
        </Grid>
        <Grid item xs={12} sm={6}>
          <Typography variant="h6" gutterBottom >
            Remetente:
          </Typography>
          <Typography gutterBottom>{senderInfo.name}</Typography>
          <Typography gutterBottom>{senderAdress}</Typography>
        </Grid>
        <Grid item xs={12} sm={6}>
          <Typography variant="h6" gutterBottom>
            Destinatário:
          </Typography>
          <Typography gutterBottom>{recipientInfo.name}</Typography>
          <Typography gutterBottom>{recipientAdress}</Typography>
        </Grid>
        <Grid item xs={12}>
          <Divider />
        </Grid>

        <Grid item xs={12}>
          <List disablePadding>
            <ListItem key={'Description'} sx={{ py: 1, px: 0 }}>
              <ListItemText primary={'Produto: ' + deliveryInfos.product} secondary={'Modalidade: ' + deliveryInfos.modality} />
              <Typography variant="subtitle1" fontSize={32} sx={{ fontWeight: 900 }}>
                {'R$' + price}
              </Typography>
            </ListItem>
          </List>
        </Grid>
        <Grid item xs={12}>
          <Button variant='contained' onClick={() => { handleBack() }}>
            Voltar
          </Button>
          <Button variant='contained' onClick={() => { handleNext() }}>
            Próximo
          </Button>
        </Grid>
      </Grid>
    </React.Fragment>
  );
}